  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body {
  width: 100vw;
  height: 100vh;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
  background-attachment: fixed;
  display: flex;
  flex-direction: column;
  align-items: center;
}
#content {
  flex: 1 1 0;
  display: flex;
  width: 100%;
  flex-direction: column;
  /* background-color: antiquewhite; */
  /* padding: 30px; */
}
.nav {
  height: 100px;
  display: flex;
  justify-content: flex-start;
  background-color: beige;
  border-bottom: 1px solid silver;
  box-shadow: 0 2px 3px silver;
}
.logo {
  margin-left: 10px;
}
.img {
  width: 100px;
}
.nav-items {
  display: flex;
  flex: 1 0 0;
}
.items {
  display: flex;
  flex: 1 0 0;
  align-items: center;
  justify-content: center;
}
.items > li {
  padding: 20px;
  list-style: none;
}
.nav-btn {
  font-size: 1.3rem;
  text-decoration: none;
  color: black;
}
.main {
  align-self: center;
  background-color: blanchedalmond;
  margin-top: 10px;
  height: 100%;
  padding: 30px;
  border-radius: 20px;
  width: 600px;
}
.main-content {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.loc {
  text-align: center;
  margin: 20px;
}
.block {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.menu-header,
.contact-heading {
  text-align: center;
  border: 10px solid orange;
  border-radius: 40px;
  width: 200px;
  outline: 5px solid red;
}
.menu {
  border: 10px solid orange;
  text-align: center;
  border-radius: 50px;
  padding: 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 10px;
}
.menu h2 {
  border: 5px solid red;
  padding: 5px;
  border-radius: 50px;
}
.salad,
.burger {
  width: 300px;
  padding: 10px;
}
.conatct-main {
  margin-top: 40px;
}
.para {
  font-size: 1.5rem;
}
